.btn {
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  width: 12.625rem;
  padding: 1rem 2.5rem;
  margin-bottom: 0.125rem;
  font-family: "Gotu", serif;
  background-color: #408f95;
  font-size: 0.875rem;
  color: #fff;
  border: 0.0625rem solid #408f95;
  text-align: center;
  position: relative;
  z-index: 1;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
}
@media screen and (min-width: 768px) {
  .btn {
    margin-bottom: 0.375rem;
  }
}

.btn::before {
  position: absolute;
  right: -0.25rem;
  top: 0.25rem;
  width: 100%;
  height: 100%;
  border: 0.0625rem solid #408f95;
  transition: 0.3s;
  content: "";
  z-index: -1;
}

.btn::after {
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

@media (any-hover: hover) {
  .btn:hover {
    color: #408f95;
  }
  .btn:hover::after {
    transform: scale(1, 1);
  }
  .btn:hover span::before,
  .btn:hover span::after {
    background-color: #408f95;
  }
}
.btn span::before,
.btn span::after {
  content: "";
  position: absolute;
  background-color: #fff;
  transition: 0.5s;
}

.btn span::before {
  top: calc(50% - 0.125rem);
  right: 2.5rem;
  transform: translateY(calc(-50% - 0.0625rem)) rotate(37deg);
  width: 0.625rem;
  height: 0.0625rem;
}

.btn span::after {
  top: 50%;
  right: 2.5rem;
  transform: translateY(-50%);
  width: 2.5rem;
  height: 0.0625rem;
}

.btn--white {
  background-color: #fff;
  color: #408f95;
  border-color: #fff;
}

.btn--white::before {
  border-color: #fff;
}

.btn--white::after {
  background: #408f95;
}

@media (any-hover: hover) {
  .btn--white:hover {
    color: #fff;
  }
  .btn--white:hover::after {
    transform: scale(1, 1);
  }
  .btn--white:hover span::before,
  .btn--white:hover span::after {
    background-color: #fff;
  }
}
.btn--white span::before,
.btn--white span::after {
  background-color: #408f95;
}

.btn-submit {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  width: 12.625rem;
  max-width: 100%;
  font-family: "Gotu", serif;
  background-color: #408f95;
  font-size: 0.875rem;
  color: #fff;
  position: relative;
  z-index: 1;
}

/* ホバー時に背景が左から右に移動するアニメーション */
.btn-submit::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background-color: #fff;
  transition: transform 0.2s ease-in-out;
  transform-origin: left;
  z-index: 0;
  pointer-events: none;
}

.btn-submit:hover::before {
  transform: translateX(100%);
}

.btn-submit input[type=submit] {
  border: 1px solid #408f95;
  color: #fff;
  cursor: pointer;
  font-size: 0.875rem;
  font-weight: normal;
  height: 100%;
  padding: 1rem 3.5rem;
  position: relative;
  text-align: left;
  width: 100%;
  z-index: 1;
  transition: color 0.3s ease-in-out;
}

/* ホバー時にテキスト色を変える */
.btn-submit:hover input[type=submit] {
  color: #408f95;
}

/* 矢印のデザイン */
.btn-submit__arrow {
  left: 50%;
  margin-left: 1.0625rem;
  position: absolute;
  width: 2.5rem;
  z-index: 2;
  border-bottom: 1px solid #fff;
  border-right: 1px solid #fff;
  height: 0.375rem;
  margin-left: 0.5rem;
  transform: skew(45deg);
  transition: border-color 0.4s ease-in-out;
}

/* ホバー時に矢印の色も変更 */
.btn-submit:hover .btn-submit__arrow {
  border-bottom: 1px solid #408f95;
  border-right: 1px solid #408f95;
}

.btn-submit::after {
  border: 1px solid #408f95;
  content: "";
  height: 100%;
  position: absolute;
  right: -0.25rem;
  top: 0.25rem;
  width: 100%;
  z-index: -999;
}

