/* 矢印 */
.faq-accordion__title {
  position: relative;
}

.faq-accordion__title::before,
.faq-accordion__title::after {
  background: #fff;
  content: "";
  height: 0.125rem;
  position: absolute;
  right: 1rem;
  top: 50%;
  width: 1.75rem;
  transition: transform 0.3s ease-in-out;
}
