    background-image: url("../images/common/deco-fishes-to-right.png");
    background-size: contain;
    background-repeat: no-repeat;
    vertical-align: middle;
    top: -7.5rem;
    left: calc(50% - min(48%, 38.75rem));
  }
}

@media screen and (min-width: 768px) {
  .voice::after {
    content: ""; /*何も入れない*/
    position: absolute;
    display: inline-block;
    width: 4.4375rem; /*画像の幅*/
    height: 10.125rem; /*画像の高さ*/
    background-image: url("../images/common/deco-seadragon.png");
    background-size: contain;
    background-repeat: no-repeat;
    vertical-align: middle;
    bottom: -4.5625rem;
